import { Component, OnInit, Inject, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import {MatDialog} from '@angular/material';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})

export class AppComponent {

  title = 'app';
  public scrollstatus = window.scrollY;
  public viewportHeight = innerHeight;
  public viewportWidth = innerWidth;
  public showMenu: boolean = false;
  public icon: string = 'menu';
  @ViewChild('landing', {read: ElementRef}) private landing: ElementRef;
  @ViewChild('projects', {read: ElementRef}) private projects: ElementRef;
  @ViewChild('skills', {read: ElementRef}) private skills: ElementRef;
  @ViewChild('about', {read: ElementRef}) private about: ElementRef;


  constructor(
    public router: Router,
    public dialog: MatDialog
  ) {}

  ngOnInit() {
  }

  ngAfterViewInit() {
    console.log(this.projects);    
    console.log(this.skills);
    console.log(this.about);
  }

  triggerMenu() {
    this.showMenu = !this.showMenu;
    if (this.showMenu) this.icon = 'close';
    else this.icon = 'menu';
  }

  getMenuHeight() {
    if (this.viewportWidth <= 768) {
      return this.viewportHeight + 'px';
    } else {
      return 'auto';
    }
  }

  checkScrollStatus(status: number) {
    return window.scrollY >= status;
  }

  getScrollStatus()
  {
    console.log(window.scrollY);
    //console.log('viewport'+this.viewportWidth);
    return +window.scrollY;
  }

  openDialogDataprotection() {
    const dialogRef = this.dialog.open(DialogContentDataprotectionDialog, {
      height: '400px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openDialogImprint() {
    const dialogRef = this.dialog.open(DialogContentImprintDialog, {
      height: '400px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  private menuItemsArray: any[] = [ 
    {"title":"Landing", "id": 0},
    {"title":"Projects", "id": 1},
    {"title":"Skills", "id": 2},
    {"title":"About", "id": 3},
    {"title":"Contact", "id": 4},
  ];

  public onMenuClose(){
    console.log("menu closed");
  }
  public onMenuOpen(){
    console.log("menu Opened");
  }
  private onItemSelect(item:any){
    console.log(item + 'projects offsetTop: ' + this.about.nativeElement.firstChild.offsetTop);
    console.log(item + 'projects clientHeight: ' + this.about.nativeElement.firstChild.clientHeight);
    
    switch (item.id) {
      case 0: window.scrollTo(0, 0); break;
      case 1: window.scrollTo(0, this.projects.nativeElement.firstChild.offsetTop + 340); break;
      case 2: window.scrollTo(0, this.skills.nativeElement.firstChild.offsetTop + 320); break;
      case 3: window.scrollTo(0, this.about.nativeElement.firstChild.offsetTop + 320); break;
    }
    
  }
}

@Component({
  selector: 'dialog-content-dataprotection-dialog',
  templateUrl: 'dialog-content-dataprotection-dialog.html',
})
export class DialogContentDataprotectionDialog {}

@Component({
  selector: 'dialog-content-imprint-dialog',
  templateUrl: 'dialog-content-imprint-dialog.html',
})
export class DialogContentImprintDialog {}



